.certified {
    background: #f4f6f8;
    // margin-top: -15px;
    padding: 60px 0 200px 0;
    > .container {
        > .title {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #333333;
            margin: 30px 0 0 0;
            padding: 0;
        }
    }
}
.certified_title {
    padding: 0;
    margin: 0;
}
.certified_title {
    // display: inline-block;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: #333333;
    // display: inline-block;
    height: 30px;
}
.certified_title_icon {
    margin: 0;
}
.certified_catalog {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    margin-top: 35px;
    > .b_button {
        margin: 0;
        grid-column: 3/3;
        margin: 50px 0 200px -100px;
        position: relative;
        > svg:nth-child(2) {
            position: absolute;
            top: 10px;
            left: 195px;
        }
    }
}
.certified_block {
    background: #fff;
    margin: 15px 15px 0 0px;
    width: 270px;
    > img {
        margin-left: -10px;
    }
}

.certified_main_title {
    margin-top: 55px;
    display: grid;
    justify-items: center;
    > .certified_title_text {
        width: 550px;
        text-align: center;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: #727272;
    }
}
.certified_title_name,
._title_name {
    display: flex;
    > span:nth-child(2) {
        display: inline-block;
        font-weight: 600;
        font-size: 16px;
        line-height: 21px;
        text-align: center;
        color: $InputText;
        background: #fff;
        width: 152px;
        height: 33px;
        box-sizing: border-box;
        padding: 6px 20px;
    }
}

///////////////////////////////////////////////
.certified_2 {
    .path {
        width: 460px;
        align-items: center;
        span {
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: #c5d2de;
        }
        .last_path {
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: #333333;
        }
    }
    .certified_main_title {
        .certified_title_name {
            > span:nth-child(2) {
                width: 270px;
            }
        }
    }
    .course_block:hover {
        opacity: 0.6;
    }
    .certified_2_main_content {
        background: #ffffff;
        box-shadow: 0px 0px 50px rgba(202, 214, 226, 0.25);
        height: 500px;
        margin: 150px 0 0 0;
        border-top-left-radius: 15% 40%;
        border-bottom-right-radius: 15% 40%;

        .container {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            .certified_2_main_content_right {
                display: grid;
                justify-content: center;
                align-items: center;
                box-sizing: border-box;
                padding: 120px 0;
                ._title {
                    margin: 0;
                    > span:nth-child(2) {
                        font-weight: bold;
                        font-size: 24px;
                        line-height: 29px;
                        text-transform: uppercase;
                        color: #333333;
                    }
                }
                .certified_2_main_content_right_text {
                    font-size: 14px;
                    line-height: 20px;
                    letter-spacing: 0.04em;
                    color: #727272;
                    width: 405px;
                }
                .b_button {
                    margin: 40px 0 0 0;
                    width: 367px;
                    svg {
                        left: 325px;
                    }
                }
            }
        }
    }
}

.certified_3 {
    .container {
        position: relative;
        .certified_title_name {
            > span:nth-child(2) {
                width: 338px;
                height: 54px;
            }
        }
        .back {
            position: absolute;
            top: 140px;
            text-decoration: none;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: $InputText;
        }
    }
}
