.my_course_main_header {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    .my_course_main_header_block {
        background: white;
        box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
        border-radius: 0 22px;
        // width: 200px;
        height: 70px;
        > p:nth-child(2) {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: $InputText;
        }
        > p:nth-child(3) {
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.04em;
            color: $InputText;
        }
    }
    .my_course_main_header_block:not(:first-child) {
        margin-left: 10px;
    }
    .my_course_main_header_block:last-child {
        .blue_list {
            padding-left: 10px;
        }
    }
    .blue_list {
        width: 34px;
        height: 34px;
        box-sizing: border-box;
        padding: 7px;
        margin: -5px 0 0 -5px;
        position: absolute;
    }
    p {
        text-align: center;
        margin: 6px 0 0 0;
    }
}

.my_course_main_content {
    ._title {
        display: flex;
        width: 190px;
        justify-content: space-between;
        align-items: center;
        > span:nth-child(2) {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #333333;
        }
    }
    .text {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: #727272;
        margin-bottom: 30px;
    }
    .my_course_main_catalog,
    .my_course_catalog {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        .course_block {
            margin-right: 15px !important;
            width: 260px;
            img {
                transform: scaleX(0.99);
            }
        }
        .course_block:last-child {
            margin-right: 0 !important;
        }
    }
    .my_course {
        position: relative;
        ._title {
            width: auto;
            > span:nth-child(2) {
                flex: 2 2 50%;
                margin-left: 15px;
            }
        }
        .b_button {
            margin: 0;
        }
    }
}
.window_title {
    position: relative;
    margin-bottom: 10px;
    width: 185px;
    height: 33px;
    margin: 0 auto 10px;
    display: flex;
    > span:nth-child(2) {
        border-radius: 0px 15px 0px 0px;
        background: #f4f6f8;
        display: inline-block;
        padding: 6px 20px;
        box-sizing: border-box;
        width: 160px;
        height: 33px;
        font-weight: 600;
        font-size: 16px;
        line-height: 21px;
        color: $InputText;
    }
    .exit {
        position: absolute;
        right: -200px;
        top: -26px;
        display: inline-block;
        width: 130px;
        height: 50px;
        background: #f4f6f8;
        border-radius: 19px 0px 19px 19px;
        display: grid;
        align-items: center;
        justify-content: end;
        box-sizing: border-box;
        padding-right: 20px;
    }
}
.window_info {
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.04em;
    color: $TextColor;
}
.course_add_window {
    width: 400px;
    height: 360px;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
    border-radius: 0px 30px;
    padding: 26px 50px 40px;
    box-sizing: border-box;
    position: absolute;
    z-index: 1;
    display: none;
    left: 200px;
    top: -200px;
    p {
        text-align: center;
        margin: 0;
    }

    input,
    input::placeholder {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
    }
    input {
        width: 300px;
        background: #f4f6f8;
        border-radius: 10px;
        height: 38px;
        box-sizing: border-box;
        padding: 9px 20px;
        border: none;
        margin-top: 10px;
    }
    input[type="file"] {
        display: none;
    }
    label {
        display: flex;
        margin: 15px 0 40px;
        align-items: center;
        > .load {
            margin-right: 10px;
        }
    }
    .b_button {
        width: 300px;
        > svg {
            left: 258px;
        }
    }
}
