.cabinet_bonus {
    ._title {
        .blue_list {
            margin-right: 15px;
        }
    }
}
.info_block {
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
    width: 270px;
    border-radius: 0 22px;
    box-sizing: border-box;
    text-align: center;
    position: relative;
    height: 70px;
}
.cabinet_bonus_info {
    width: 555px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin: 0;

    .info_block:first-child {
        grid-column: 1/1;
        p {
            margin: 0px;
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.04em;
            color: $InputText;
        }
        > p:nth-child(2) {
            font-weight: bold;
            font-size: 36px;
            line-height: 43px;
            color: #76ba1b;
        }
    }
    .info_block:nth-child(2) {
        // display: flex;
        width: 270px;
        display: grid;
        grid-template-columns: repeat(6, 1fr);
        align-items: center;
        .info_block_left,
        .info_block_right {
            span {
                display: block;
            }
        }
        .info_block_left {
            grid-column: 1/3;
            justify-self: end;
        }
        .info_block_right {
            justify-self: start;
            grid-column: 4/6;
        }
        .info_block_center {
            font-weight: 500;
            font-size: 24px;
            line-height: 29px;
            text-align: center;
            text-transform: uppercase;
            color: $InputText;
        }
        .top {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: $InputText;
        }
        .bottom {
            font-size: 12px;
            line-height: 16px;
            text-align: center;
            letter-spacing: 0.04em;
            color: $InputText;
        }
    }
    .b_button {
        margin: 15px 0 0 0;
        grid-column: 2/3;
        width: 270px;
        svg {
            left: 226px;
        }
    }
    .green_list,
    .blue_list {
        position: absolute;
        left: -5px;
        top: -5px;
    }
    .blue_list {
        padding: 6px;
        width: 34px;
        height: 34px;
        box-sizing: border-box;
    }
}
.green_list {
    background: #76ba1b;
    border-radius: 0 15px;
    display: inline-block;
    width: 34px;
    height: 34px;
    box-sizing: border-box;
    padding: 10px;
}

.sales {
    margin-top: 60px;
    ._title {
        .blue_list {
            margin-right: 15px;
        }
    }
    .stats {
        margin: 60px 0 0 -25px;
    }
}
.sales_blocks {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    .info_block:nth-child(3) {
        > p:nth-child(3) {
            text-align: center;
            margin-left: 100px;
            > span {
                margin-left: 5px;
            }
        }
    }
    .info_block {
        position: relative;
        .blue_list {
            position: absolute;
            left: -5px;
            top: -5px;
            padding-top: 4px;
            box-sizing: border-box;
        }
        p {
            margin: 0px;
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.04em;
            color: $InputText;
            height: 40px;
        }
        > p:nth-child(2) {
            margin-top: 10px;
            font-weight: bold;
            font-size: 36px;
            line-height: 43px;
            color: $InputText;
            > span:nth-child(2) {
                font-weight: normal;
                font-size: 16px;
                text-transform: uppercase;
            }
        }
        > p:nth-child(3) {
            > span {
                font-size: 12px;
                line-height: 16px;
                text-align: center;
                letter-spacing: 0.04em;
                color: $TextColor;
                font-weight: normal;
                display: inline-block;
                margin-left: 17px;
            }
        }
    }
}
.modal_window {
    width: 400px;
    display: none;
    height: 363px;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
    border-radius: 0px 30px;
    justify-content: center;
    position: absolute;
    left: 50%;
    top: 20%;
    img {
        justify-self: center;
    }
    ._title_name {
        margin-top: 25px;
        justify-self: center;
        > span:nth-child(2) {
            width: 200px;
            background: #f4f6f8;
            border-radius: 0px 15px 0px 0px;
        }
    }
    .money_list {
        display: flex;
        align-items: center;
        text-align: center;
        width: 82px;
        height: 24px;
        justify-self: center;
        background: #76ba1b;
        border-radius: 12px 0px;
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: #ffffff;

        span {
            margin-left: 10px;
        }
        svg {
            margin-left: 5px;
        }
    }
    .close_modal_window {
        position: absolute;
        left: 400px;
    }
    .p_a {
        position: absolute;
        right: 24px;
        top: 200px;
    }
    p {
        margin: 10px 0 0 0;
        font-size: 12px;
        line-height: 18px;
        letter-spacing: 0.04em;
        color: #c5d2de;
        height: 20px;
    }
    .b_button {
        margin: 30px 0 0 0;
        width: 300px;
        svg {
            left: 256px;
        }
    }
    input {
        background: #f4f6f8;
        border-radius: 10px;
        border: none;
        margin-top: -15px;
        width: 300px;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
        padding: 8px 20px;
        box-sizing: border-box;
    }
    .input_card {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        input {
            width: 145px;
            margin-right: 10px;
        }
    }
}
