.cabinet_questions_and_answers {
    ._title {
        .blue_list {
            margin-right: 15px;
        }
    }
    .big_accordion {
        width: 830px;
        .contentBx_title::after {
            padding: 10px 0 0 0;
        }
        .contentBx_title {
            width: 830px;
            display: block;
            padding-top: 10px;
            box-sizing: border-box;
            font-weight: 600;
            font-size: 16px;
            line-height: 21px;
            color: $InputText;
            pointer-events: none;
        }
        .contentBx_title:hover {
            background: white;
        }
        .content {
            width: 830px;
            li {
                width: 830px;
            }
            box-sizing: border-box;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: #727272;
        }
        .contentBx {
            width: 830px;
        }
        .contentBx:hover {
            background: #fcfcfd;
        }
    }
    .big_accordion .contentBx.accordion_active .content {
        padding: 15px 25px;
    }
}
