.course_lesson_finished {
    position: relative;
    margin-top: 100px;
    width: 1140px;
    display: grid;
    .finish {
        position: absolute;
        display: inline-block;
        background: #76ba1b;
        box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
        border-radius: 0px 10px;
        top: -285px;
        left: 440px;
        z-index: 1;
        width: 81px;
        height: 20px;
        padding: 2px 15px;
        box-sizing: border-box;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.04em;
        color: #ffffff;
    }
    ._title {
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        text-transform: uppercase;
        color: #333333;
        margin-top: 150px;
        .blue_list {
            margin-right: 15px;
        }
    }
    ._title_name {
        justify-self: center;
    }
    .proggress_lesson {
        justify-self: center;
        > span:nth-child(1) {
            font-weight: 800;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #76ba1b;
        }
        > span:nth-child(3) {
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: #c5d2de;
        }
    }
    .more_courses {
        display: flex;
        flex-direction: row-reverse;
        .course_block {
        }
    }
}
.course_finish_modal_window {
    display: grid;
    left: 30%;
    ._title_name {
        > span:nth-child(2) {
            height: 42px;
            width: 261px;
            font-style: normal;
            font-weight: 600;
            font-size: 16px;
            line-height: 21px;
            color: $InputText;
            text-align: left;
            padding: 6px 15px;
        }
    }
}

.course_lesson_homework,
.course_lesson_homework_done {
    margin: 80px 0px 0 340px;
    display: none;
    ._title_name {
        margin-left: -40px;
        > span:nth-child(2) {
            width: 187px;
            padding: 5px 0;
        }
    }
    .homework_text {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: #727272;
    }
    .b_button {
        margin: 0;
        justify-self: center;
    }
    .deadline {
        display: flex;
        align-items: center;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: #9293ba;
        > svg {
            margin-right: 10px;
        }
    }
    .homework_footer {
        margin-top: 20px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        align-items: center;
    }
    .homework_next_lesson {
        justify-self: end;
        text-decoration: none;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: #9293ba;
    }
    .done {
        background: #76ba1b;
    }
}
.course_lesson_homework_done {
    display: none;

    .mark_block {
        background: #ffffff;
        box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
        border-radius: 25px 0px;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        padding: 0px 15px 0 30px;
        box-sizing: border-box;
        margin-top: 30px;
        .see {
            justify-self: end;
            display: flex;
            align-items: center;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: #c5d2de;
            svg {
                margin-left: 15px;
            }
        }
        .mark {
            display: flex;
            align-items: center;
            > span:nth-child(1) {
                font-size: 16px;
                line-height: 21px;
                color: #c5d2de;
                margin-right: 8px;
                display: inline-block;
            }
            span {
                font-weight: 600;
                font-size: 16px;
                line-height: 21px;
                color: $InputText;
            }
        }
    }
}
