.top_side {
	display: flex;
	height: 40px;
	align-items: center;
	> a {
		flex: 1 1 33%;
		font-size: 12px;
		line-height: 14px;
		text-transform: uppercase;
		color: $TextColor;
		text-decoration: none;
	}
	> a:nth-child(2) {
		margin-left: -710px;
	}
	> .tel {
		> a {
			text-align: end;
			text-decoration: none;
			color: #727272;
			margin-left: 10px;
		}
	}
}

.main {
	box-shadow: 0px 0px 16px rgba(202, 214, 226, 0.5);
	height: 80px;
	box-sizing: border-box;
	.container {
		display: grid;
		grid-template-columns: repeat(3, 1fr);
		// margin-top: auto;
		align-items: center;

		.icon {
			display: inline-block;
		}
		> .left {
			margin-top: 13px;
		}
		> .center {
			// margin-left: 155px;
			// width: 600px;
			box-sizing: border-box;
			> ul {
				margin: 0;
				display: flex;
				// display: grid;
				// grid-template-columns: repeat(4, 1fr);
				list-style: none;
				width: 500px;
				> li {
					flex: 1 1 25%;
					// height: 80px;
					padding: 30px 0;
					position: relative;
					> a {
						font-weight: 600;
						font-size: 12px;
						line-height: 14px;
						color: $TextColor;
						text-decoration: none;
						box-sizing: border-box;
						padding-left: 20px;

						> .icon {
							position: relative;
							> svg {
								position: absolute;
								left: -5px;
								top: -10px;
							}
						}
						> p {
							box-sizing: border-box;
							padding-left: 20px;
						}
					}

					> a:first-child {
						position: relative;
						// padding-left: 20px;
						> p {
							position: absolute;
							top: -12px;
							left: 20px;
						}
					}
					> a:not(:first-child) {
						margin-left: -40px;
					}
					> .sub_menu {
						list-style: none;
						padding: 30px 20px;
						background-color: $hover;
						position: absolute;
						left: 1px;
						top: 78px;
						transform-origin: 0 0;
						transform: translateY(-100px);
						opacity: 0;
						visibility: hidden;
						transition: all 0.3s ease-in-out;
						a {
							font-size: 14px;
							line-height: 20px;
							letter-spacing: 0.04em;
							color: $InputText;
							text-decoration: none;
						}
						> li {
							padding: 10px 0px;
							// position: relative;
							> .sub_sub_menu {
								transform-origin: 0 0;
								transform: translateY(-100px);
								opacity: 0;
								visibility: hidden;
								transition: all 0.3s ease-in-out;

								list-style: none;
								position: absolute;
								left: 111px;
								background-color: $hover;
								top: 0px;
								padding: 30px 10px;
								li {
									padding: 10px 20px 10px 0;
								}
							}
						}
						> li:hover > .sub_sub_menu {
							transform: translateY(0);
							opacity: 1;
							visibility: visible;
						}
					}
				}
				> li:hover > .sub_menu {
					transform: translateY(0);
					opacity: 1;
					visibility: visible;
				}
				> li:hover {
					background: #f4f6f8;
				}
			}
		}
		> .right {
			// margin-left: -110px;
			width: 275px;
			justify-self: end;
			text-align: end;
		}
	}
}
.korzina_button {
	display: inline-block;
	width: 130px;
	height: 38px;
	border: 1.5px solid $InputText;
	box-sizing: border-box;
	border-radius: 19px 0px 19px 19px;
	background-color: #fff;
	font-size: 14px;
	line-height: 20px;
	text-align: center;
	letter-spacing: 0.04em;
	color: $InputText;
	text-decoration: none;
	text-align: center;
	padding: 8px 0;
}
.korzina_button:hover {
	background: #f4f6f8;
}
