@keyframes arrow_anim {
    from {
        transform: rotate(-90deg);
    }
    to {
        transform: rotate(45deg);
    }
}

@keyframes menu {
    0% {
        max-height: 0;
        // transform: scaleY(0);
    }
    100% {
        transform: scaleY(100%);
        max-height: 500px;
        // padding-bottom: 200px;
    }
}

.accordion {
    width: 270px;
    margin-top: 0;
    box-sizing: border-box;
    background: #fff;
    padding: 0;
    height: 520px;
    box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
    border-radius: 0 20px 0 20px;
    a {
        text-decoration: none;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.04em;
        color: $InputText;
    }
}
.contentBx {
    position: relative;
    margin: 0px 20px;
    list-style: none;
}
.label {
    position: relative;
    padding: 10px 25px;
    display: block;
    cursor: pointer;
    line-height: 21px;
    color: $InputText;
    margin-left: -20px;
    width: 220px;
}
.accordion_active .label {
    background: $InputText;
    border-radius: 0 20px;
    color: white;
}
.accordion_title {
    padding: 20px 60px;
    font-size: 18px;
    line-height: 24px;
    color: $InputText;
}
.label:hover {
    background: #f4f6f8;
    font-weight: 600;
    font-size: 16px;
    line-height: 21px;
    color: $InputText;
}
.accordion_active .label:hover {
    line-height: 21px;
    font-weight: normal;
    background: $InputText;
}

.content {
    list-style: none;
    position: relative;
    max-height: 0;
    transition: all 0.5s ease-in-out;
    overflow: hidden;
    background: #f4f6f8;
    margin-left: -20px;
    width: 250px;
    // padding-left: 20px;
    > li {
        margin: 10px 0 0 40px;
    }
}
.accordion .contentBx.accordion_active .label::before {
    content: "";
    display: block;
    width: 8px;
    height: 8px;
    border: 1px solid #c5d2de;
    border-right: transparent;
    border-bottom: transparent;
    // transform: rotate(45deg);
    animation: arrow_anim;
    animation-duration: 0.5s;
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
    position: absolute;
    top: 40%;
    right: 10px;
}
.accordion .contentBx.accordion_active .content {
    // height: auto;
    max-height: 500px;
    padding: 10px;
}

///////////////////////////////////
.big_accordion {
    width: 710px;
    margin: 20px 0 0 40px;
    background: none;
    height: auto;
    > .contentBx {
        width: 710px;
        margin-left: 0;
        margin-top: 20px;
        background: white;
        border-radius: 22px 0px 22px 0px;
        .label {
            margin-left: 0;
            display: inline;
            padding: 0;
        }
        .contentBx_title {
            box-sizing: border-box;
            height: 44px;
            width: 710px;
            margin: 0;
            display: flex;
            align-items: center;
            padding-left: 30px;
            position: relative;
        }
        .contentBx_title::before {
            display: none;
        }
        .contentBx_title::after {
            display: inline-block;
            width: 44px;
            height: 44px;
            content: "+";
            background: $InputText;
            border-radius: 0 20px 0 20px;
            transform: matrix(-1, 0, 0, 1, 0, 0);
            position: absolute;
            right: 0px;
            top: 0;
            color: #fff;
            box-sizing: border-box;
            padding: 5px 12px 20px 12px;
            text-align: center;
            // align-self: stretch;
            font-size: 30px;
            transition: all 0.5s ease-in-out;
        }
        .contentBx_title:hover {
            background: #fff;
            border-radius: 22px 0px 22px 0px;
            font-weight: normal;
        }
        > ul {
            padding: 0;
        }
        > .content {
            width: 710px;
            box-sizing: border-box;
            margin: 0;
            background: #fff;
            padding: 0;
            border-bottom-right-radius: 22px;
            transform-origin: top;
            // transform: scaleY(0);
            max-height: 0;
            transition: all 0.5s;
            > li {
                margin: -10px 0 0 -10px;
                width: 710px;
                box-sizing: border-box;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #727272;
                padding: 10px 20px 10px 30px;
            }
            > li:hover {
                background: $hover;
            }
        }
    }
}
.big_accordion .accordion_active .label {
    background: #fff;
    border-radius: 22px 0px 22px 0px;
    color: $InputText;
}
.big_accordion .accordion_active .label:hover {
    line-height: 21px;
    font-weight: normal;
    background: #fff;
    color: $InputText;
}

.big_accordion .contentBx.accordion_active .label::before {
    display: none;
    opacity: 0;
    visibility: hidden;
}
.big_accordion .contentBx.accordion_active .contentBx_title {
    border-bottom: 1px solid #f4f6f8;
}
.big_accordion .contentBx.accordion_active .contentBx_title::after {
    display: inline-block;
    content: "";
    width: 12px;
    height: 3px;
    background: $InputText;
    position: absolute;
    right: 20px;
    top: 50%;
    border-radius: 0;
    padding: 0;
}
.big_accordion .contentBx.accordion_active .content {
    // height: auto;
    // height: 0;
    // animation-name: menu;
    // animation-fill-mode: forwards;
    // animation-duration: 0.5s;
    // animation-iteration-count: 1; // padding: 10px 10px 130px 10px;
    // padding-bottom: auto !important;
    // transform: scaleY(100%);
    max-height: 1000px;
}

