.cabinet_notifications {
    ._title {
        .blue_list {
            margin-right: 15px;
        }
    }
}
.notifications_list {
    list-style: none;
    .notification_not_checked_li {
        // width: 15px;
        // height: 73px;
        // background: #ff3e3e;
        // position: absolute;
        // left: 0px;
        // border-radius: 22px 0 0 0;
        // z-index: -1;
        border-left: 6px solid #ff3e3e;
        margin-left: -6px;
    }
    li {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        width: 770px;
        height: 49px;
        background: #ffffff;
        border-radius: 22px 0;
        padding: 12px 30px;
        margin-bottom: 20px;
        position: relative;
        z-index: 1;

        * {
            margin: 0;
        }
        .left {
            > p:first-child {
                margin-bottom: 10px;
                font-weight: 600;
                font-size: 16px;
                line-height: 21px;
                color: $InputText;
            }
            > p {
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: #c5d2de;
            }
            .notification_course_name {
                display: flex;
                align-items: center;
                > svg {
                    margin-right: 10px;
                }
            }
        }
        .right {
            justify-self: end;
            margin-right: -30px;
            > p {
                margin-bottom: 10px;
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: #c5d2de;
                display: flex;
                align-items: center;
                justify-content: end;
                > svg {
                    margin-right: 10px;
                }
            }
            .nofication_button {
                border: 1.5px solid $InputText;
                box-sizing: border-box;
                border-radius: 17px 0px 17px 17px;
                width: 135px;
                height: 35px;
                display: inline-block;
                padding: 8px 40px;
                box-sizing: border-box;
                text-decoration: none;
            }
        }
    }
}
