.cabinet_wish_list {
    ._title {
        .blue_list {
            margin-right: 15px;
        }
    }
}

.wish_list_catalog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
}
.course_block {
    position: relative;
}

.close_course {
    display: inline-block;
    position: absolute;
    top: 0px;
    right: 0px;
    width: 30px;
    height: 30px;
    border-radius: 10px 0px 10px 10px;
    background: #fff;
    padding: 0px 8px 13px;
    box-sizing: border-box;
}
.close_course::after {
    content: "";
    display: inline-block;
    width: 15px;
    height: 2px;
    transform: rotate(-45deg);
    background: $InputText;
    position: absolute;
    top: 12px;
}
.close_course::before {
    top: 12px;
    position: absolute;
    content: "";
    display: inline-block;
    width: 15px;
    height: 2px;
    transform: rotate(45deg);
    background: $InputText;
}
