.course_not_paid {
    min-height: 1300px;
}
.path {
    display: flex;
    width: 355px;
    justify-content: space-between;
    align-items: center;
}
.previous_path {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: #c5d2de;
}
.current_path {
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: #333333;
}

.blue_list_section {
    background: $InputText;
    border-top-right-radius: 15% 85%;
    border-bottom-left-radius: 15% 85%;
    height: 260px;
    margin-top: 30px;
    box-sizing: border-box;
    padding: 80px 0;
}
.blue_list_section_header {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    align-items: center;
    margin-top: -20px;
    .white_title {
        width: 420px;
        margin-bottom: 0;
        > span:nth-child(2) {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #ffffff;
        }
    }
    .lesson_count {
        margin-left: 20px;
        font-weight: 600;
        font-size: 16px;
        line-height: 21px;
        color: #c5d2de;
    }
    .stars {
        justify-self: end;
    }
}

.tags {
    list-style: url(../img/circle.png);
    margin-left: 30px;
    margin-top: 10px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    > li {
        font-size: 18px;
        line-height: 24px;
        color: #f4f6f8;
    }
}
.blue_list_section_footer {
    display: flex;
    justify-content: space-between;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: #c5d2de;
    margin-left: 45px;
    // align-items: center;
    // align-content: center;
    justify-items: center;
    height: 20px;
    span {
        display: inline-block;
        margin-left: 10px;
    }
    .last_update,
    .author,
    .share {
        position: relative;
        display: flex;
    }
    .share {
        width: 200px;
        justify-content: space-between;
    }

    // justify-content: center;
}

.frame {
    width: 270px;
    margin-top: -30px;
    background: #fff;
    box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
    border-radius: 0 30px 0 30px;
    box-sizing: border-box;
    padding-top: 30px;
    display: inline-block;
    z-index: 1;
    .frame_title {
        margin: -20px 0 10px 0;
    }
    .frame_accordion {
        padding: 0;
        list-style: none;
        margin-bottom: 0;
        ul {
            list-style: none;
        }
        .content {
            margin-left: -40px;
            width: 270px !important;
            padding-left: 0;
            transition: all 0s;
            display: block;
            z-index: 1;
            li {
                width: 270px;
                height: 32px;
                margin: 0;
                padding: 10px 0 10px 30px;
                box-sizing: border-box;
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: #727272;
                display: flex;
                align-items: center;
                span {
                    flex: 2 2 50%;
                }
                svg {
                    margin-right: 5px;
                    fill: #9293ba;
                }
            }

            .course_lesson_active {
                color: white;
                background: $InputText;
                svg {
                    path {
                        fill: white;
                    }
                }
            }
            > li:first-child {
                margin-top: 5px;
            }
        }
        .content:hover {
            font-weight: normal;
        }
        .label:hover {
            font-weight: normal;
        }
        ._content {
            padding-left: 0;
            > li {
                // padding-left: 40px;
                height: 32px;
                box-sizing: border-box;
                width: 270px !important;
                padding: 10px 0 10px 40px;
                margin-left: 0;
            }
            > li:hover {
                background: $hover;
            }
        }
        > li {
        }
        > li:not(:nth-child(2)) {
            margin-top: 30px;
        }
        .b_button {
            margin: 30px 0 0 0;
            width: 270px;
            border-radius: 0 30px 0 30px;
            background: $InputText;
            position: relative;
            span:nth-child(3) {
                font-weight: 500;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #ffffff;
                position: relative;
            }
            > svg {
                position: absolute;
                left: 95px;
                top: 15px;
            }
            .locked::after {
                // left: 240px;
                top: 20px;
            }
            // > svg:nth-child(2) {
            //     position: absolute;
            //     left: 240px;
            //     top: 15px;
            // }
        }
    }
    .frame_title {
        font-weight: 600;
        font-size: 16px;
        line-height: 21px;
        color: $InputText;
        text-align: center;
        position: relative;
    }
    .frame_title::after {
        position: absolute;
        right: 20px;
        top: 5px;
        content: "";
        width: 10px;
        height: 10px;
        border: 1.4px solid #c5d2de;
        transform: rotate(-45deg);
        border-top: transparent;
        border-right: transparent;
        display: inline-block;
        transition: all 0.5s ease-in-out;
    }
    .arrow_active::after {
        transform: rotate(135deg);
    }
}
._content_hidden {
    padding: 0;
    max-height: 0;
    overflow: hidden;
    transform: scaleY(0);
    opacity: 0;
    visibility: hidden;
    transform-origin: 0 0;
    transition: all 1s ease-in-out;
    > li {
        margin: 30px 0 0 25px;
        font-weight: 600;
        line-height: 21px;
        color: $InputText;
    }
    > li:first-child {
        margin-top: 10px !important;
    }
}
.frame_acive {
    // animation-name: menu;
    // animation-duration: 1s;
    // animation-fill-mode: forwards;
    // animation-iteration-count: 1;
    // height: auto;
    max-height: 1200px;
    opacity: 1;
    visibility: visible;
    transform: scaleY(100%);
}
