.all_courses {
    background: #f4f6f8;
    padding: 60px 0 150px;
    > .container {
        > .all_courses_title {
            > .all_courses_title_icon {
                display: inline-block;
            }
        }
        > .title {
            margin-top: 30px;
            padding: 0;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #333333;
            > span:nth-child(2) {
                margin-left: 15px;
            }
        }
    }
}
.courses {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    width: 850px;
    grid-column: 2/4;
    // margin: 0;
    > .b_button {
        margin: 0;
        justify-self: center;
        justify-items: center;
        grid-column: 2/2;
        position: relative;
        > svg:nth-child(2) {
            position: absolute;
            left: 195px;
            top: 10px;
        }
    }
}
.course_block {
    background: #fff;
    margin: 0px 20px 30px 0;
    width: 270px;
    > img {
        margin-left: -10px;
    }
}
.main_section {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
}
