.course_lesson_content {
    display: inline-block;
}
.frame {
    position: absolute;
    // top: 40px;
}

.course_lesson_theory {
    min-height: 1000px;
    position: relative;
    margin-left: 300px;
    display: none;
    .course_lesson_abs {
        // position: absolute;
        ._title_name {
            margin-top: 80px;
            .blue_list {
            }
            > span:nth-child(2) {
                width: 550px;
            }
        }
    }
}
.lesson_theory_text {
    margin: 15px 0 0 45px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: #727272;
}
.lesson_theory_second_img,
.lesson_theory_first_img {
    width: 787px;
    display: inline-block;
    margin: 40px 0 40px 45px;
}
.lesson_theory_first_img {
    height: 193px;
}
.lesson_theory_second_img {
    height: 350px;
    background-image: url("../img/lesson_second.png");
    position: relative;
    svg {
        position: absolute;
        display: none;
        left: 45%;
        top: 40%;
    }
}
.lesson_theory_second_img:hover {
    background-image: url("../img/lesson_second_hover.png");
    svg {
        display: block;
    }
}
.course_lesson_abs {
    display: grid;
    .b_button {
        margin: 20px 0 0 0;
        justify-self: center;
    }
    .course_lesson_go_test {
        justify-self: end;
        text-decoration: none;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #c5d2de;
        margin: -30px 0 150px 0;
    }
}
