.cabinet_info_section {
    ._title {
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        text-transform: uppercase;
        color: #333333;
    }
}
.cabinet_my_certificates {
    ._title {
        .blue_list {
            margin-right: 15px;
        }
    }
}

.my_certificates_catalog {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    .course_block {
        height: 270px;
        .course_block_date {
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.04em;
            color: #c5d2de;
            display: inline-flex;
            align-items: center;
            svg {
                margin-right: 8px;
            }
        }
        button {
            position: relative;
            top: 0px;
            left: 30px;
            font-weight: 500;
            font-size: 12px;
            line-height: 14px;
            text-align: center;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: $InputText;

            svg {
                margin-left: 12px;
            }
        }
    }
}
.progress_line {
    display: block;
    height: 5px;
    margin: -5px 0 0 0;
    background: #76ba1b;
}
