.authorization {
    // margin-top: -15px;
    background: #f4f6f8;
    box-sizing: border-box;
    padding-bottom: 180px;
    padding-top: 0;
    > .container {
        > .authorization_title {
            width: 100px;
            margin: 0;
            align-items: flex-end;
        }
        // margin-top: 60px;
        > .title {
            margin-top: 30px;
            padding: 0;
            margin-bottom: 40px;
            > .blue_list {
                margin: 0;
            }
            > span {
                display: inline-block;
                margin-left: 15px;
                font-weight: bold;
                font-size: 24px;
                line-height: 29px;
                text-transform: uppercase;
                color: #333333;
            }
        }
    }
}

.authorization_title_icon {
    display: inline-block;
    margin-top: 60px;
}
#enter {
    display: inline-block;
    margin-left: 20px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: #333333;
}
.main_content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    > img {
        margin-left: -70px;
    }
    > .main_form {
        margin-left: 150px;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        width: 300px;
        margin-top: 50px;
        > input {
            width: 300px;
            height: 40px;
            box-sizing: border-box;
            padding: 10px 20px;
            border: none;
            border-radius: 10px;
        }
        > input::placeholder {
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: $TextColor;
        }
        > #forgot_pass {
            text-align: end;
            width: 300px;
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.04em;
            color: $TextColor;
            text-decoration: none;
        }
        > .b_button {
            margin: 0;
            justify-self: center;
        }
        > #registr {
            font-size: 12px;
            line-height: 16px;
            letter-spacing: 0.04em;
            color: $InputText;
            text-decoration: none;
            text-align: center;
        }
        .sing_in {
            width: 300px;
            height: 120px;
            background: #ffffff;
            border-radius: 10px;
            > p {
                text-align: center;
                font-size: 18px;
                line-height: 24px;
                color: $InputText;
            }
        }
    }
}
.soc_button {
    display: inline-block;
    background: linear-gradient(180deg, #18acfe 0%, #0164e1 100%);
    border-radius: 22px;
    color: white;
    font-size: 12px;
    line-height: 16px;
    text-align: center;
    letter-spacing: 0.04em;
    box-sizing: border-box;
    width: 130px;
    padding: 10px 0;
    margin-left: 15px;
    text-decoration: none;
    position: relative;
    > svg {
        position: absolute;
        top: 7px;
        left: 15px;
    }
}
.soc_button:hover {
    background: #6cbaf7;
}

.sec {
    margin-left: 10px;
}

.forgot_password_block {
    display: none;
    width: 400px;
    // height: 300px;
    padding-bottom: 20px;
    background-color: #fff;
    position: absolute;
    left: 40%;
    top: 40%;
    box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
    border-radius: 0px 30px;
    // opacity: 1;
    .container {
        width: 300px;
        margin: 0 auto;
        .forgot_password_title {
            // padding-top: 25px;
            > p:first-child {
                background: #f4f6f8;
                border-radius: 0px 15px 0px 0px;
                // box-sizing: border-box;
                display: flex;
                align-items: center;
                width: 240px;
                // margin-left: 70px;
                padding: 0 20px 0 0;
                > span {
                    padding: 6px 20px;
                    margin-left: 20px;
                    display: inline-block;
                    padding: 15px 0;
                    font-weight: 600;
                    font-size: 16px;
                    line-height: 21px;
                    color: $InputText;
                    box-sizing: border-box;
                }
                margin-bottom: 0;
            }
            > span {
                font-size: 12px;
                line-height: 16px;
                text-align: center;
                letter-spacing: 0.04em;
                color: $TextColor;
                display: inline-block;
                width: 240px;
                text-align: center;
                // margin-left: 70px;
            }
            > p:nth-child(2) {
                margin-top: 0;
                font-size: 12px;
                line-height: 16px;
                text-align: center;
                letter-spacing: 0.04em;
                color: $TextColor;
            }
        }
        .forgot_password_form {
            > input {
                background: #f4f6f8;
                border-radius: 10px;
                width: 260px;
                height: 18px;
                padding: 10px 20px;
                border: none;
                margin: 30px 0 25px -20px;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: $TextColor;
            }
            > input::placeholder {
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: $TextColor;
            }
            > .not_m {
                margin: -10px 0 25px -20px;
                // margin: 5px 0 5px -20px;
            }
            > .b_button {
                margin: 0 0 0 -20px;
                width: 300px;
                height: 14px;
                padding: 15px 0;
                > svg {
                    // position: relative;
                    left: 260px;
                    top: 0px;
                }
                > span {
                    margin-top: 0;
                }
            }
        }
    }
}
.exit {
    margin-left: 310px;
}
.bc {
    background: gray;
    opacity: 0.5;
}

