footer {
    // margin-top: 40px;
    display: grid;
    text-align: center;
    justify-content: center;
    box-shadow: 0px 0px 50px rgba(202, 214, 226, 0.25);
    > .icon {
        margin-top: 40px;
    }
    > .footer_content {
        > .nav {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            margin-top: 25px;
            > a {
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: $TextColor;
                text-decoration: none;
            }
            > a:nth-child(5) {
                grid-column-start: 1;
                grid-column-end: 3;
                margin: 15px 10px 0px 100px;
            }
            > a:nth-child(6) {
                grid-column-start: 3;
                grid-column-end: 4;
                margin: 15px 0 0 0;
            }
            > a:hover {
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: $InputText;
            }
        }
        > .soc {
            margin-top: 20px;
            > a {
                display: inline-block;
                margin-left: 20px;
                > svg:hover > path {
                    fill: $TextColor;
                }
            }
        }
    }
}
