.registr_form {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    width: 300px;
    margin-top: -10px;
    > input {
        width: 280px;
        margin-bottom: 10px;
        border-radius: 10px;
        border: none;
        padding-left: 20px;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
    }
    > input::placeholder {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
    }
    > .agree {
        display: inline-block;
        width: 300px;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.04em;
        color: $TextColor;
        width: 270px;
        display: inline-block;
        margin-left: 30px;
        position: relative;
    }
    > .agree::before {
        content: "";
        display: inline-block;
        width: 18px;
        height: 18px;
        background: url("../img/agree.png") no-repeat;
        position: absolute;
        left: -25px;
    }
    > input:checked + .agree::before {
        background: url(../img/agree_+.png);
    }
    // > .agree:hover::before {
    //     background: url("../img/agree_hover.png");
    // }
    > .g-recaptcha + input {
        display: none;
    }

    .b_button {
        margin: 40px 0 0 0;
        justify-self: center;
    }
    .has_ac {
        justify-self: center;
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.04em;
        color: $InputText;
        text-decoration: none;
    }
}
