main {
    background: #f4f6f8;
    padding-top: 60px;
}
.course_info {
    padding-bottom: 10px;
    .container {
        > .course_info_title {
            display: flex;
            justify-content: space-between;
            width: 350px;
            justify-items: center;
            align-items: center;
            > * {
                display: inline-block;
            }
            > #enter {
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #c5d2de;
            }
            > span {
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #333333;
            }
        }
    }
}
.white_list {
    display: inline-block;
    width: 28px;
    height: 28px;
    background: #fff;
    border-radius: 0px 50% 0px 50%;
    margin-right: 15px;
}
.course_info_main_section {
    box-sizing: border-box;
    padding: 80px 0;
    width: 1440px;
    height: 360px;
    background: $InputText;
    margin-left: -150px;
    border-top-right-radius: 15% 45%;
    border-bottom-left-radius: 15% 45%;
    color: #fff;
    > .container {
        > .course_info_main_section_title {
            margin-top: 0;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            display: inline-block;
            margin-right: 20px;
            margin-bottom: 0;
        }
        > .lesson_count {
            display: inline-block;
            font-weight: 600;
            line-height: 21px;
            color: #c5d2de;
        }
        .tags {
            list-style-image: url(../img/circle.png);
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            width: 500px;
            margin: 0px 0 0px 30px;
            > li {
                font-size: 18px;
                line-height: 24px;
                color: #f4f6f8;
            }
        }
        .course_info_section {
            display: flex;
            justify-content: space-between;
            width: 560px;
            margin-left: 45px;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: #c5d2de;
            svg {
                margin-right: 10px;
            }
        }
    }
}
.course_info_buttons {
    margin-top: 50px;
    display: flex;
    justify-content: space-between;
    width: 480px;
    margin-left: 50px;
    align-items: center;
    > .soc {
        display: flex;
        justify-content: space-between;
        width: 80px;
    }
    > .white_button {
        width: 200px;
        height: 44px;
        background: #fff;
        box-sizing: border-box;
        display: inline-block;
        // padding: 13px 40px;
        margin: 0;
        position: relative;
        border-radius: 22px;
        padding: 0;
        > svg {
            left: 160px;
            position: absolute;
        }
        > span {
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            // text-align: center;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: $InputText;
            display: inline-block;
            // height: 44px;
            padding: 13px 40px;
            // box-sizing: border-box;
        }
    }
    > .white_button:hover {
        background: $hover;
    }

    > a:nth-child(2) {
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #ffffff;
        text-decoration: none;
    }
}
.course_info_description {
    margin-top: 80px;
    position: relative;
    > .container {
        position: relative;
        > .description_text {
            width: 710px;
            position: relative;
            display: grid;
            grid-template: repeat(1, 1fr);
            > .description_text_content {
                width: 710px;
                height: 120px;
                overflow: hidden;
                margin-left: 40px;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #727272;
                > .opacity_min {
                    opacity: 0.7;
                }
                > .opacity_max {
                    opacity: 0.4;
                }
            }
        }
    }
}

.accordion_button {
    justify-self: end;
    width: 44px;
    height: 44px;
    background: $InputText;
    border-radius: 25px 0 25px;
    position: relative;
}
.accordion_button::after {
    content: "+";
    font-size: 40px;
    color: white;
    position: absolute;
    left: 10px;
    // top: 10px;
}

.course_info_block {
    display: inline-block;
    width: 360px;
    // float: right;
    position: absolute;
    right: 0px;
    top: -360px;
    background: white;
    border-radius: 0px 50px 0px 0;
    > img {
        width: 360px;
    }
    > .course_info_block_container {
        // width: 330px;
        display: flex;
        flex-wrap: wrap;
        margin-left: 25px;
        grid-template-columns: repeat(2, 1fr);
        margin-top: 30px;
        > .price_info {
            flex: 1 1 50%;
            > p {
                margin: 0;
            }
            > .gray_price {
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: #c5d2de;
            }
            > .course_price {
                font-weight: bold;
                font-size: 24px;
                line-height: 29px;
                text-transform: uppercase;
                color: $InputText;
            }
            > .course_bonus {
                font-weight: 500;
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: $TextColor;
            }
        }
        > .stars {
            align-self: center;
        }
        > .course_info_block_buttons {
            display: block;
            margin-top: 15px;
            > .b_button {
                display: block;
                flex: 1 2 50%;
                width: 307px;
                margin: 0;
                > svg {
                    left: 265px;
                }
            }
            > .button {
                display: block;
                width: 307px;
                height: 44px;
                border: 1.5px solid $InputText;
                box-sizing: border-box;
                border-radius: 22px 0px 22px 22px;
                padding: 13px 80px;
                margin-top: 28px;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: $InputText;
                text-decoration: none;
            }
            > .button:hover {
                background: #f4f6f8;
            }
        }
        > .course_info_on {
            > .course_info_on_name {
                font-size: 18px;
                line-height: 24px;
                color: $InputText;
            }
            > ul {
                padding: 0;
                > li {
                    display: flex;
                    align-items: center;
                    // justify-content: space-between;
                    margin-top: 8px;
                    font-weight: 600;
                    line-height: 21px;
                    color: $InputText;
                    > svg {
                        margin-right: 10px;
                    }
                }
            }
        }
    }
}

.b_button:hover {
    background: linear-gradient(180deg, #4056de 0%, #1e2f9a 100%);
}
