.description_text_content.opacity_active {
    height: auto !important;
    span {
        opacity: 1 !important;
    }
}
.description_text_content.opacity_active + .opacity_button::after {
    content: "-";
    left: 12px;
    top: -17px;
    font-size: 60px;
}
.course_material {
    > ._title_name {
        // width: 300px;
        > span:nth-child(2) {
            width: 190px;
        }
    }
}
.course_material_container {
    width: 750px;
    .accordion .contentBx.accordion_active .content {
        padding: 0 !important;
    }
}
.content_checked::before {
    background: url(../img/checked.png) no-repeat;
    display: inline-block;
    width: 12px;
    height: 12px;
    content: "";
    margin-right: 5px;
}
.content_video::before {
    background: url(../img/video_icon.png) no-repeat;
    display: inline-block;
    width: 12px;
    height: 12px;
    content: "";
    margin-right: 5px;
}
.content_test::before {
    // list-style: url(../img/test.png) !important;
    background: url(../img/test.png) no-repeat;
    display: inline-block;
    width: 12px;
    height: 12px;
    content: "";
    margin-right: 5px;
}
.content_circle::before {
    width: 12px;
    height: 12px;
    content: "";
    display: inline-block;
    background: $InputText;
    border-radius: 50%;
    margin-right: 5px;
}
.content_test,
.content_video,
.content_checked,
.content_circle {
    display: flex;
    align-items: center;
    text-align: center;
    position: relative;
}
.content_checked {
    opacity: 0.7;
}
.content_checked::after,
.content_test::after,
.content_video::after,
.content_circle::after,
.locked::after {
    display: inline-block;
    content: "";
    width: 10px;
    height: 10px;
    background: url(../img/unlocked.png) no-repeat;
    position: absolute;
    right: 20px;
    background-size: contain;
}
.locked::after {
    background: url(../img/white_locked.png);
}
.content_test::after,
.content_video::after,
.content_circle::after {
    background: url(../img/locked.png) no-repeat;
}

.course_info_comments {
    margin-top: 90px;
    > ._title_name {
        // width: 250px;
        box-sizing: border-box;
        > span:nth-child(2) {
            display: inline-block;
            padding: 6px 20px;
            width: 200px;
        }
    }
    > .korzina_button {
        float: right;
        box-sizing: border-box;
        padding: 8px 5px;
    }
    > .comments_container {
        margin: 20px 0 0 43px;
        > .comments_average {
            display: flex;
            width: 100%;
            > .left {
                flex: 1 1 50%;
                > * {
                    margin: 0;
                }
                > p:nth-child(1) {
                    font-size: 12px;
                    line-height: 16px;
                    letter-spacing: 0.04em;
                    color: #c5d2de;
                }
                > #average_num {
                    font-weight: bold;
                    font-size: 24px;
                    line-height: 29px;
                    text-transform: uppercase;
                    color: $InputText;
                }
            }
            > .right {
                flex: 1 1 50%;
                justify-self: end;
                text-align: end;
                // display: inline;
                > * {
                    display: inline;
                }
                > .comments_count {
                    font-size: 12px;
                    line-height: 16px;
                    letter-spacing: 0.04em;
                    color: #c5d2de;
                }
            }
        }
    }
}

.comments {
    margin-top: 15px;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    .comment {
        background: #fff;
        padding-bottom: 25px;
        margin-top: 25px;
        .comment_header {
            display: grid;
            grid-template-columns: repeat(9, 1fr);
            width: 100%;
            align-items: center;
            .profile_img {
                width: 44px;
                height: 44px;
                background-size: contain;
                background: no-repeat;
                border-radius: 50% 0 50% 0;
                margin-top: -20px;
            }
            .profile_name {
                grid-column: 2/3;
                font-weight: 600;
                line-height: 21px;
                color: $InputText;
            }
            .comment_date {
                font-size: 12px;
                line-height: 16px;
                letter-spacing: 0.04em;
                color: $TextColor;
                width: 100px;
            }
            .stars {
                grid-column: 9/10;
            }
        }
        .comment_text {
            box-sizing: border-box;
            padding: 0 25px;
            margin: 5px 0 0 0;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: #727272;
        }
    }
    .b_button {
        margin: 40px 0 0 0;
        justify-self: center;
        position: relative;
        > svg:nth-child(2) {
            position: absolute;
            left: 192px;
            top: 10px;
        }
    }
}

.interesting {
    margin: 150px 0 200px 0;
    ._title_name {
        background: none;
        > span:nth-child(2) {
            background: none;
            width: auto;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #333333;
        }
    }
    .interesting_catalog {
        margin: 60px 0 0 0;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
    }
}
