.cabinet_invite {
    .blue_list {
        margin-right: 15px;
    }
    ._title_name {
        > span:nth-child(2) {
            width: 520px;
        }
    }
}
.invite_center {
    display: grid;
    justify-content: center;
    .invite_soc {
        width: 300px;
        height: 194px;
        padding: 20px 15px;
        box-sizing: border-box;
        background: #fff;
        margin: 25px 105px;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        justify-content: center;
        text-align: center;
        * {
            margin: 0;
        }
        .soc_buttons {
            display: grid;
            grid-template-columns: repeat(2, 1fr);
            .soc_button {
                margin-bottom: 10px;
                > svg {
                    top: 10px;
                    left: 14px;
                }
            }
            .soc_button:hover {
                opacity: 0.7;
            }
            .soc_button:nth-child(3) {
                background: linear-gradient(95.75deg, #5450c5 -25.76%, #cf2e82 47.7%, #f6cf74 113.87%);
            }
            .soc_button:nth-child(4) {
                background: #4d7db1;
                > svg {
                    left: 9px;
                    top: 12px;
                }
            }
        }
    }
    .href {
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.04em;
        color: #c5d2de;
        display: flex;
        align-items: center;
        margin: 8px 0 20px 0;
        > svg {
            margin-right: 10px;
        }
    }
}
