.cabinet_profile {
    position: relative;
    ._title_name {
        > span:nth-child(2) {
            width: 170px;
            margin-right: 20px;
        }
    }
}
.personal_info {
    width: 615px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    margin-top: 25px;
    input,
    select {
        background: #ffffff;
        border-radius: 10px;
        width: 300px;
        height: 38px;
        box-sizing: border-box;
        padding: 9px 20px;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
        border: none;
        margin-bottom: 10px;
    }
    input::placeholder,
    select::placeholder {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
    }
    select {
        background: #fafafb;
    }
    > input:last-child {
        background: #fafafb;
    }
    .personal_info_date {
        grid-row: 2/4;
        grid-column: 2/3;
        label {
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: $TextColor;
        }
        input {
            padding: 0 0 0 12px;
            background: #fafafb;
        }
        > input:nth-child(2) {
            width: 45px;
            margin-left: 5px;
        }
        > input:nth-child(3),
        input:nth-child(4) {
            width: 66px;
            // margin-left: 5px;
        }
    }
}
._title_name_2 {
    margin-top: 40px;
    > span:nth-child(2) {
        width: 100px !important;
    }
}
.personal_info_change_password {
    display: grid;
    input {
        margin-top: 25px;
        width: 300px;
        height: 38px;
        box-sizing: border-box;
        padding: 9px 20px 15px 20px;
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
        background: #ffffff;
        border: none;
        border-radius: 10px;
    }
    input::placeholder {
        line-height: 20px;
        font-size: 14px;
        letter-spacing: 0.04em;
        color: $TextColor;
    }
    .b_button {
        margin: 25px 0 0 0;
        padding-top: 10px;
        box-sizing: border-box;
    }
}
#personal_info_password {
    display: none;
}
#personal_info_password::placeholder {
    font-size: 30px;
}

.personal_info_change_password_suc {
    width: 400px;
    height: 295px;
    background: #fff;
    justify-content: center;
    position: absolute;
    left: 30%;
    display: none;
    top: 20%;
    ._title_name {
        height: 30px;
        > span:nth-child(2) {
            width: 250px;
        }
    }
    img {
        margin-left: 60px;
        margin-top: -20px;
    }
}
.close_modal_window {
    width: 50px;
    height: 50px;
    background: #f4f6f8;
    border-radius: 19px 0px 19px 19px;
    position: relative;
    left: 350px;
    top: 0;
}
.close_modal_window::after {
    width: 25px;
    height: 2px;
    background: $InputText;
    content: "";
    display: inline-block;
    transform: rotate(-45deg);
    position: absolute;
    top: 20px;
    left: 10px;
}
.close_modal_window::before {
    width: 25px;
    height: 2px;
    background: $InputText;
    content: "";
    display: inline-block;
    transform: rotate(45deg);
    position: absolute;
    left: 10px;
    top: 20px;
}
