.korzina {
    background: #f4f6f8;
    // margin-top: -15px;
    padding-bottom: 150px;
    position: relative;
    padding-top: 0;
}
.blue_list {
    background: #111e6c;
    border-radius: 0 15px 0 15px;
    width: 28px;
    height: 28px;
    display: inline-block;
}
.title {
    padding-top: 60px;
    margin-top: 0;
}
.title_text {
    font-weight: bold;
    font-size: 24px;
    line-height: 29px;
    text-transform: uppercase;
    color: #333333;
}
.my_courses {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    margin-top: 40px;
    > .course {
        background-color: #fff;
        display: grid;
        grid-template-columns: repeat(4, 1fr);
        justify-items: end;
        align-items: center;
        margin-bottom: 25px;
        padding-right: 15px;
        box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
        border-radius: 25px 0px;
        > img {
            border-radius: 20px 0px 20px 0px;
            justify-self: start;
        }
        > .course_name {
            font-weight: 600;
            line-height: 21px;
            color: #333333;
        }
        > .price {
            font-weight: 600;
            font-size: 18px;
            line-height: 21px;
            color: $InputText;
        }
    }
}
.total_cost {
    text-align: end;
    > span:first-child {
        font-size: 14px;
        line-height: 20px;
        letter-spacing: 0.04em;
        color: $TextColor;
    }
    > span {
        font-weight: 600;
        font-size: 18px;
        line-height: 21px;
        color: $InputText;
    }
}
.form_not_auth {
    margin-top: 60px;
    // display: grid;
    display: none;
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
    > p {
        text-align: center;
        > .f_n_a {
            font-weight: 600;
            line-height: 21px;
            color: $InputText;
            margin-left: 20px;
        }
    }
    > .b_button {
        width: 300px;
        margin: 30px 0 15px 0;
        > svg {
            left: 256px;
        }
    }
    > .sing_in {
        background-color: #fff;
        width: 300px;
        height: 120px;
        box-sizing: border-box;
        padding-top: 20px;
        margin-top: 20px;
        border-radius: 10px;
        > p {
            text-align: center;
        }
    }
}
.form_not_auth_reg {
    border: 1.5px solid $InputText;
    box-sizing: border-box;
    border-radius: 22px 0px 22px 22px;
    width: 300px;
    display: inline-block;
    padding: 13px 100px;
    text-decoration: none;
}

.with_auth {
    margin-top: 60px;
    > .w_a {
        font-weight: 600;
        line-height: 21px;
        color: $InputText;
        position: relative;
        > span:nth-child(2) {
            background: #ffffff;
            border-radius: 0px 15px 0px 0px;
            display: inline-block;
            width: 95px;
            height: 35px;
            position: absolute;
            box-sizing: border-box;
            padding: 6px 20px;
        }
    }
    ul {
        list-style-image: url(../img/agree.png);
        > li {
            > a {
                text-decoration: none;
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: $InputText;
            }
        }
    }
    > .button {
        margin-top: 40px;
        display: grid;
        grid-template-columns: repeat(1, 1fr);
        justify-items: center;
        .b_button {
            width: 300px;
            margin: 0;
            background: #76ba1b;
            > svg {
                left: 256px;
            }
        }
        .pay:hover {
            background: #92d33a;
        }
        > a:nth-child(2) {
            margin-top: 28px;
            text-decoration: none;
            box-sizing: border-box;
            padding: 14px 70px;
            display: inline-block;
            border: 1.5px solid #111e6c;
            box-sizing: border-box;
            border-radius: 22px 0px 22px 22px;
        }
        > a:nth-child(2):hover {
            background: white;
        }
    }
}

.paid {
    position: absolute;
    background-color: white;
    display: none;
    // display: grid;
    left: 70%;
    top: 5%;
    grid-template-columns: repeat(1, 1fr);
    // justify-content: center;
    width: 400px;
    height: 380px;
    box-sizing: border-box;
    text-align: center;
    justify-items: center;
    > .exit {
        margin-left: 320px;
    }
    > .title {
        display: flex;
        justify-content: center;
        margin-top: 0;
        padding: 0;
        > span:nth-child(2) {
            font-weight: 600;
            line-height: 21px;
            color: $InputText;
            background: #f4f6f8;
            border-radius: 0px 15px 0px 0px;
            display: inline-block;
            padding: 6px 20px;
            height: 33px;
            width: 245px;
            box-sizing: border-box;
        }
    }
    > .b_button {
        margin: 0;
        width: 300px;
        height: 44px;
        > svg {
            left: 256px;
        }
    }
}
