$TextColor: #9293ba;
$InputText: #111e6c;
$hover: #f4f6f8;

@import "header";
button {
    border: none;
}
@keyframes left {
    from {
        transform: translateX(-300px);
        opacity: 0;
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}
@keyframes right {
    from {
        transform: translateX(300px);
        opacity: 0;
    }
    to {
        opacity: 1;
        transform: translateX(0px);
    }
}

@import url("https://fonts.googleapis.com/css2?family=Roboto&display=swap");
* {
    font-family: sans-serif;
}
.wrapper {
    margin: 0 auto;
    width: 1440px;
}
.container {
    margin: 0 auto;
    width: 1140px;
}
.main_index {
    background: #fff;
    padding: 0;
}
main {
    .main_page {
        display: flex;
        > .left_side {
            display: block;
            margin-left: 140px;
            justify-content: space-around;
            > .title {
                font-weight: bold;
                font-size: 48px;
                line-height: 65px;
                color: #333333;
                margin-top: 170px;
                height: 120px;
                margin-bottom: 0;
            }
            > .b_button {
                display: flex;
                background: linear-gradient(180deg, #192eac 0%, #111e6c 100%);
                border-radius: 22px;
                width: 220px;
                font-weight: 500;
                font-size: 14px;
                margin-left: 0;
                justify-content: center;
                align-items: center;
                line-height: 17px;
                text-align: center;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: #ffffff;
                text-decoration: none;
                position: relative;
                height: 44px;
                > svg {
                    position: absolute;
                    left: 180px;
                    // margin-left: 66px;
                }
            }
            > .b_button:hover {
                background: linear-gradient(180deg, #4056de 0%, #1e2f9a 100%);
            }
            > .gray {
                font-size: 18px;
                line-height: 24px;
                color: #727272;
            }
        }
        > .right_side {
            flex: 1 1 50%;
            text-align: end;
        }
    }
}

.second_section {
    background: #111e6c;
    height: 200px;
    border-top-right-radius: 15% 50%;
    border-bottom-left-radius: 15% 50%;
    display: flex;
    > .container {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        > .block {
            display: flex;
            // text-align: center;
            align-items: center;
            font-weight: 600;
            font-size: 16px;
            line-height: 21px;
            /* or 131% */
            color: #ffffff;
            width: 340px;
            text-decoration: none;
        }
        > .block:not(:first-child) {
            margin-left: 65px;
        }
    }
}
.gray_text {
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.04em;
    color: $TextColor;
    position: relative;
    margin-left: 10px;
}
.gray_text::after {
    content: "";
    display: block;
    width: 10px;
    height: 10px;
    position: absolute;
    border-radius: 50%;
    top: 2px;
    left: -3px;
    background: #e6f0fa;
}
input:checked + .gray_text::after {
    background: $InputText;
}
.third_section {
    margin-top: 150px;
    .container {
        display: block;
        > .title {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #333333;
        }
        > .catalog {
            display: grid;
            grid-template-columns: repeat(4, 1fr);
            // justify-content: center;
            // align-items: center;
            // align-content: center;
            justify-items: center;
        }
    }
}
.b_button {
    display: inline-block;
    background: linear-gradient(180deg, #192eac 0%, #111e6c 100%);
    border-radius: 22px;
    width: 220px;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: #ffffff;
    text-decoration: none;
    position: relative;
    height: 44px;
    margin: 60px 0 0 470px;
    span {
        display: inline-block;
        margin-top: 15px;
    }
    > svg {
        position: absolute;
        left: 180px;
        // margin-left: 66px;
    }
}
.catalog_block,
.course_block {
    margin: 10px 12px 10px -10px;
    // margin: 10px 15px 10px 0;
    border-radius: 0px 20px;
    box-shadow: 0px 0px 15px rgba(68, 71, 83, 0.08);
    position: relative !important;
    height: 300px;
    width: 285px;
    box-sizing: border-box;
    text-decoration: none;
    > * {
        padding: 0 10px;
    }
    > img {
        transform: scaleX(1.01);
    }
    > input {
        display: none;
    }

    > .book_name {
        font-weight: 600;
        font-size: 16px;
        line-height: 21px;
        color: #333333;
    }
    > .gray_price {
        font-size: 12px;
        line-height: 16px;
        letter-spacing: 0.04em;
        color: #c5d2de;
        margin: 0 0 3px 0;
    }
    > .price {
        font-weight: 600;
        font-size: 18px;
        line-height: 21px;
        display: flex;
        align-items: center;
        color: $InputText;
        margin: 0px;
    }
}
.catalog_button {
    display: inline-block;
    background: #f4f6f8;
    border-radius: 17px 0px 17px 17px;
    font-weight: 500;
    font-size: 12px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $InputText;
    width: 110px;
    height: 35px;
    position: absolute;
    left: 140px;
    top: 250px;
}

.fourth_section {
    margin-top: 160px;
    background: $InputText;
    height: 300px;
    border-top-left-radius: 15% 45%;
    border-bottom-right-radius: 15% 45%;
    padding-top: 200px;
    > .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        > .info_text {
            margin-top: -80px;
            width: 430px;
            > .fourth_title {
                .fourth_icon {
                    display: inline-block;
                }
                > p {
                    margin: 0px;
                    display: inline-block;
                    font-weight: bold;
                    font-size: 24px;
                    line-height: 29px;
                    text-transform: uppercase;
                    color: #ffffff;
                }
            }
            > p {
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #ccdceb;
            }
            > .white_button {
                margin-top: 40px;
                margin-left: 0;
                display: inline-block;
                width: 370px;
                background-color: #fff;
                text-align: center;
                padding: 14px 0;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                text-align: center;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: $InputText;
                text-decoration: none;
                border-top-left-radius: 22px 22px;
                border-bottom-left-radius: 22px 22px;
                border-bottom-right-radius: 22px 22px;
                position: relative;
                > svg {
                    position: absolute;
                    left: 325px;
                    top: 0px;
                }
            }
            .white_button:hover {
                background: $hover;
            }
        }
        > .slider {
            display: flex;
            align-items: center;
            width: 600px;
            margin-top: -100px;
            > .left,
            > .right {
                display: inline-block;
                z-index: 1;
                transition: all 0.3s ease-in-out;
                border-radius: 50%;
            }
            > .right {
                flex: 1 1 25%;
                margin-left: -50px;
                transform: rotate(180deg);
            }
            > .slide {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                > .catalog_block {
                    background-color: #fff;
                    width: 270px;
                    margin-right: 40px;
                    transition: all 0.5s ease-in-out;
                    animation-duration: 2.5s;
                    animation-iteration-count: 1;
                    animation-fill-mode: forwards;
                    overflow: hidden;
                    > img {
                        margin-left: -12px;
                        transform: scaleY(1.06);
                        transform: scaleX(1.06);
                    }
                    border-radius: 0 20px;
                }
            }
        }
    }
}
