    // margin-top: 60px;
    position: relative;
    border-top-right-radius: 15% 45%;

    > .right_image {
        position: absolute;
        left: 1305px;
        top: -25px;
    }
    > .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        > .slider {
            display: flex;
            align-items: center;
            width: 600px;
            margin-top: 100px;
            > .left,
            > .right {
                display: inline-block;
                z-index: 1;
                transition: all 0.3s ease-in-out;
                border-radius: 50%;
            }
            > .right {
                flex: 1 1 25%;
                margin-left: -50px;
                transform: rotate(180deg);
            }
            > .slide {
                display: grid;
                grid-template-columns: repeat(2, 1fr);
                > .catalog_block {
                    background-color: #fff;
                    width: 270px;
                    margin-right: 40px;
                    transition: all 0.5s ease-in-out;
                    animation-duration: 2.5s;
                    animation-iteration-count: 1;
                    animation-fill-mode: forwards;
                    overflow: hidden;
                    > img {
                        margin-left: -12px;
                        transform: scaleY(1.06);
                        transform: scaleX(1.06);
                    }
                    border-radius: 0 20px;
                }
            }
        }
        > .soon {
            margin: 120px 0 0 110px;
            width: 405px;
            > .title {
                font-weight: bold;
                font-size: 24px;
                line-height: 29px;
                text-transform: uppercase;
                color: #333333;
                > svg {
                    margin-right: 5px;
                }
            }
            > .soon_text {
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #727272;
            }
            > .b_button {
                width: 370px;
                margin: 40px 0 0 0;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                text-align: center;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: #ffffff;
                > svg {
                    margin-left: 150px;
                }
            }
        }
    }
}
.six_section {
    margin-top: 150px;
    > .container {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        > .right_side {
            margin-left: 100px;
            > .title {
                font-weight: bold;
                font-size: 24px;
                line-height: 29px;
                text-transform: uppercase;
                color: #333333;
                > svg {
                    margin-right: 5px;
                }
            }
            > .text {
                font-size: 14px;
                line-height: 20px;
                letter-spacing: 0.04em;
                color: #727272;
            }
            > .b_button {
                width: 370px;
                margin: 40px 0 0 0;
                font-weight: 500;
                font-size: 14px;
                line-height: 17px;
                text-align: center;
                letter-spacing: 0.04em;
                text-transform: uppercase;
                color: #ffffff;
                > svg {
                    margin-left: 150px;
                }
            }
        }
    }
}

.news {
    height: 460px;
    border-top-right-radius: 15% 50%;
    border-bottom-left-radius: 15% 50%;
    background: $InputText;
    margin-top: 150px;
    padding: 100px 0;
    > .container {
        display: block;
        > .title {
            padding: 0;
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #ffffff;
        }

        > .news_slider {
            display: grid;
            grid-template-columns: repeat(5, 1fr);
            align-items: center;
            > .slide {
                grid-column-start: 2;
                grid-column-end: 5;
            }
            > .right {
                justify-self: end;
            }

            > .slide {
                display: flex;
                margin-left: -210px;
                overflow: hidden;
                width: 1140px;

                > .news_block {
                    text-decoration: none;
                    margin-right: 20px;
                    width: 240px;
                    background-color: #fff;
                    box-shadow: 0px 4px 12px rgba(68, 71, 83, 0.1);
                    border-radius: 20px;
                    padding: 0 10px 30px;
                    transition: all 1.5s ease-in-out;
                    height: 280px;
                    > .news_title {
                        font-weight: 600;
                        font-size: 16px;
                        line-height: 21px;
                        color: #333333;
                    }
                    > .news_content {
                        font-size: 12px;
                        line-height: 16px;
                        letter-spacing: 0.04em;
                        color: #727272;
                        margin: 0px;
                    }
                    > .date {
                        font-size: 12px;
                        margin: 15px 0 0 0;
                        line-height: 16px;
                        letter-spacing: 0.04em;
                        color: #c5d2de;
                        display: inline-block;
                    }
                    > button {
                        background: #f4f6f8;
                        border-radius: 17px 0px 17px 17px;
                        font-weight: 500;
                        font-size: 12px;
                        line-height: 14px;
                        text-align: center;
                        letter-spacing: 0.04em;
                        text-transform: uppercase;
                        color: $InputText;
                        border: none;
                        margin-left: 50px;
                        width: 110px;
                        height: 35px;
                    }
                    > img {
                        margin-left: -13px;
                    }
                }
            }
        }
    }
}
.read:hover {
    background: #d3dce4;
}
.white_button:hover {
    background: $hover;
}
.white_button {
    box-sizing: border-box;
    margin-top: 50px;
    width: 370px;
    display: inline-block;
    background: white;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    text-align: center;
    letter-spacing: 0.04em;
    text-transform: uppercase;
    color: $InputText;
    margin-left: 400px;
    height: 44px;
    padding: 13px 120px;
    border-radius: 22px;
    position: relative;
    text-decoration: none;
    > svg {
        // margin-left: 150px;
        position: absolute;
        top: 0px;
        left: 330px;
    }
}
.read {
    z-index: 1;
}
.pay:hover {
    background: #92d33a;
}
.course_new,
.in_process,
.course_load,
.course_suc {
    position: absolute;
    width: 80px;
    height: 24px;
    border-radius: 10px;
    background: #eea310;
    display: flex;
    align-items: center;
    text-align: center;
    font-size: 12px;
    line-height: 16px;
    letter-spacing: 0.04em;
    color: #f4f6f8;
    top: 15px;
    left: 10px;
}
.course_new::before,
.in_process::before,
.course_load::before,
.course_suc::before {
    margin-right: 10px;
    content: "new";
    color: #eea310;
    box-sizing: border-box;
    padding-left: 1px;
    text-transform: uppercase;
    display: flex;
    text-align: center;
    align-items: center;
    width: 12px;
    font-weight: 800;
    font-size: 4px;
    line-height: 4px;
    text-align: center;
    letter-spacing: 0.04em;
    height: 12px;
    background: white;
    border-radius: 50%;
}
.in_process {
    background: #f4f6f8;
    color: #9293ba;
    width: 100px;
}
.in_process::before {
    background: #c5d2de;
    color: white;
    content: "...";
    font-size: 10px;
    padding: 0 0 4px 0;
}
.course_suc {
    color: #fff;
    background: #76ba1b;
}
.course_suc::before {
    content: "v";
    color: #76ba1b;
    width: 12px;
    font-size: 8px;
    padding-left: 2px;
    margin-right: 1px;
}
