.not_found {
    .container {
        display: grid;
    }
    .path {
        width: 83px;
    }
    ._title {
        margin-top: 30px;
        position: relative;
        .blue_list {
            margin-right: 15px;
        }
        > span:nth-child(2) {
            font-weight: bold;
            font-size: 24px;
            line-height: 29px;
            text-transform: uppercase;
            color: #333333;
        }
        a {
            position: absolute;
            top: 100px;
            font-weight: 500;
            font-size: 14px;
            line-height: 17px;
            letter-spacing: 0.04em;
            text-transform: uppercase;
            color: $InputText;
            text-decoration: none;
            left: 0;
        }
    }
    img {
        justify-self: center;
    }
    .b_button {
        margin: 10px 0 150px 0;
        justify-self: center;
    }
}
