.cabinet .container {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    min-height: 1300px;
    margin-top: 90px;
}
.cabinet_user_info {
    height: 100px;
    width: 280px;
}
.cabinet_info_section {
    height: 100px;
    grid-column: 2/5;
    margin-left: 40px;
}
#_file {
    display: none;
}

.prof_head_image {
    width: 190px;
    height: 190px;
    border-radius: 50%;
    background: white;
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    justify-items: center;
    align-items: center;
    margin: 0 auto;
    position: absolute;
    top: -90px;
    left: 50px;
    .profile_image {
    }
    > label::before {
        content: "";
        display: inline-block;
        width: 10px;
        height: 10px;
        background: url(../img/load.png);
        margin-right: 4px;
    }
    > label {
        font-size: 10px;
        line-height: 14px;
        letter-spacing: 0.04em;
        color: $TextColor;
    }
}
.bonus_count {
    background: #76ba1b;
    border-radius: 12px 0px;
    width: 55px;
    height: 24px;
    display: inline-block;
    box-sizing: border-box;
    padding-top: 5px;
    margin-left: 10px;
}
.money {
    display: inline-block;
    width: 22px;
    height: 14px;
    background: url(../img/money.png);
}
.red_list {
    width: 23px;
    height: 23px;
    background: #ff3e3e;
    border-radius: 12px 0px;
    display: grid;
    box-sizing: border-box;
    padding-top: 2px;
    text-align: center;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.04em;
    color: #ffffff;
}
.cabinet_user_info_frame {
    min-height: 570px;
    background: #ffffff;
    box-shadow: 0px 0px 25px rgba(202, 214, 226, 0.25);
    border-radius: 0 22px 0 22px;
    > ul {
        padding: 0;
        list-style: none;
        > li {
            padding: 12px 25px;

            cursor: pointer;
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: $TextColor;
            position: relative;

            .red_list {
                position: absolute;
                left: -12px;
                top: 10px;
            }
            > span:nth-child(1) {
                svg {
                    margin-right: 15px;
                    width: 16px;
                }
            }
        }
        > li:hover {
            background: #fafafa;
            color: $InputText;
        }
    }
}
.li_active {
    background: #f4f6f8;
    > span {
        svg {
            path {
                fill: #111e6c;
            }
        }
    }
    .li_exit {
        svg {
            path {
                fill: #ff3e3e;
            }
        }
    }
}
.cabinet_info_section {
    min-height: 1000px;
    > div {
        display: none;
    }
    > .block_active {
        display: block;
    }
}
.prof_head {
    position: relative;
    box-sizing: border-box;
    padding-top: 90px;
    .user_name {
        font-weight: 600;
        font-size: 24px;
        line-height: 29px;
        color: $InputText;
        text-align: center;
        margin-bottom: 13px;
    }
    .bonus {
        text-align: center;
        margin-top: 13px;
        > span:nth-child(1) {
            font-size: 14px;
            line-height: 20px;
            letter-spacing: 0.04em;
            color: $TextColor;
        }
        #bonus_count {
            font-weight: 600;
            font-size: 14px;
            line-height: 20px;
            text-align: center;
            letter-spacing: 0.04em;
            color: #ffffff;
        }
    }
}
.cabinet {
    position: relative;
}
.exit_menu {
    width: 400px;
    height: 250px;
    background: #fff;
    padding: 30px 30px 40px 50px;
    box-sizing: border-box;
    display: none;
    z-index: 1;
    position: absolute;
    left: 50%;
    top: 20%;
    .window_title {
        width: auto;
        > span:nth-child(2) {
            width: 350px;
        }
        .exit {
            top: -30px;
            right: -70px;
            width: 100px;
            box-sizing: border-box;
            padding-right: 20px;
        }
    }
    a {
        display: block;
        width: 300px;
        height: 44px;
        text-decoration: none;
        box-sizing: border-box;
    }
    .red_button {
        border-radius: 22px;
        background: #ff3e3e;
        position: relative;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        padding: 13px 124px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: #ffffff;
        margin: 40px 0 15px;
        > span:nth-child(2) {
        }
        > svg {
            position: absolute;
            left: 256px;
            top: 0;
        }
    }
    .wh_button {
        border: 1.5px solid $InputText;
        box-sizing: border-box;
        border-radius: 22px 0px 22px 22px;
        font-weight: 500;
        font-size: 14px;
        line-height: 17px;
        letter-spacing: 0.04em;
        text-transform: uppercase;
        color: $InputText;
        padding: 13px 50px;
    }
}
